body {
  font-family: "Montserrat", sans-serif;
  font-size: 1.6rem; }
  body.body-fixed {
    overflow: hidden; }

p {
  font-size: 2rem; }

a {
  text-decoration: none; }

img {
  max-width: 100%; }

h1 {
  font-size: 4rem; }

h2 {
  font-size: 3.6rem; }

h3 {
  font-size: 3.2rem; }

h1, h2, h3 {
  margin: 0; }

.container {
  max-width: 120rem;
  width: 95%;
  margin: 0 auto; }

.flex {
  display: flex; }

.grid {
  display: grid; }

.text-align-center {
  text-align: center; }

.header {
  background-color: #4cb8b3; }
  .header .header__container {
    flex-direction: column;
    align-items: center; }
    @media (min-width: 768px) {
      .header .header__container {
        flex-direction: row;
        justify-content: space-between; } }
  .header h1, .header a {
    text-align: center;
    color: #fff;
    padding: 2rem 0; }
  .header.fixed {
    position: fixed;
    z-index: 2;
    width: 100%;
    box-shadow: 0 .3rem .4rem #444; }

.header__nav {
  gap: 3rem; }
  .header__nav a {
    font-weight: 700;
    font-size: 1.8rem; }
    .header__nav a:hover {
      color: #f53756; }

.video {
  position: relative;
  max-height: 60rem;
  overflow: hidden; }
  .video .overlay {
    position: absolute;
    width: 100%;
    height: 100%;
    background-color: rgba(245, 55, 86, 0.2);
    background: linear-gradient(90deg, #020024 0%, rgba(9, 9, 121, 0.288953) 81%, #00d4ff 100%);
    flex-direction: column;
    justify-content: center;
    align-items: center; }
  .video video {
    display: block;
    width: 100%; }

.video__container h2 {
  font-size: 5.5rem; }

.video__container span {
  background-color: #f53756;
  padding: 1rem; }

.video__container h3 {
  font-size: 2rem;
  padding: 2rem 0; }

.video__container h2, .video__container span {
  color: #fff; }

.description {
  gap: 2rem;
  padding: 5rem;
  flex-direction: column; }
  @media (min-width: 768px) {
    .description {
      flex-direction: row; }
      .description picture, .description div {
        flex: 1; } }
  .description div strong {
    display: block;
    margin: 2rem 0; }
  .description div p {
    line-height: 1.5;
    margin: 0; }

.description__text {
  display: flex;
  flex-direction: column;
  justify-content: center; }

.lineup {
  background-color: #f53756; }
  .lineup h3, .lineup .lineup__day {
    padding: 2rem 0;
    color: #fff;
    text-align: center; }
  .lineup h3 {
    padding-top: 4rem; }

@media (min-width: 768px) {
  .lineup__container {
    grid-template-columns: repeat(2, 1fr);
    column-gap: 2rem; } }

.lineup__container:last-of-type {
  padding-bottom: 2rem; }

.lineup__info_container {
  flex-direction: column;
  padding: 2rem 0;
  margin: 1rem 0; }
  .lineup__info_container p {
    margin: 0;
    padding: 2rem; }

.lineup__info {
  flex-direction: column;
  align-items: center;
  margin-bottom: 1rem; }
  .lineup__info img {
    width: 20rem; }

.lineup__info--yellow {
  background-color: #fef46d; }

.lineup__info--green {
  background-color: #4cb8b3; }

.bg-darker-yellow {
  background-color: #fef03a; }

.bg-darker-green {
  background-color: #3b9691; }

.gallery {
  margin-bottom: 4rem; }
  .gallery h3 {
    text-align: center;
    padding: 4rem 0; }

.gallery__list {
  list-style: none;
  padding: 0;
  margin: 0 auto;
  grid-template-columns: repeat(2, 1fr);
  gap: 2rem; }
  @media (min-width: 768px) {
    .gallery__list {
      grid-template-columns: repeat(4, 1fr); } }
  @media (min-width: 1200px) {
    .gallery__list {
      grid-template-columns: repeat(6, 1fr); } }
  .gallery__list li {
    text-align: center; }
    .gallery__list li:hover {
      cursor: pointer;
      transform: scale(1.05); }

/* JS Section */
.overlay-gallery {
  position: fixed;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  display: grid;
  place-items: center; }

.btn-close {
  background-color: #fff;
  width: 6rem;
  height: 6rem;
  display: grid;
  place-items: center;
  color: #f53756;
  font-weight: 700;
  margin: 0;
  border-radius: 50%; }
  .btn-close:hover {
    cursor: pointer;
    background-color: #e6e6e6; }

.tickets {
  background-color: #4cb8b3; }
  .tickets h3 {
    color: #fff;
    padding: 4rem 0;
    text-align: center; }

.tickets__container {
  flex-direction: column;
  gap: 4rem;
  padding-bottom: 4rem; }
  @media (min-width: 768px) {
    .tickets__container {
      flex-direction: row;
      justify-content: center; } }

.tickets__info {
  width: 100%; }
  .tickets__info ul {
    list-style: none;
    padding: 0;
    margin: 0; }
    .tickets__info ul li {
      padding: 1rem 1rem;
      margin: 0;
      background-color: #fff; }
      .tickets__info ul li:nth-of-type(odd) {
        background-color: #e6e6e6; }

.tickets__title {
  color: #fff;
  text-align: center;
  padding: 1rem 0;
  margin: 0; }

.tickets__title:nth-of-type(1) {
  font-size: 2.5rem; }

.tickets__title:nth-of-type(2) {
  background-color: #f53756; }

.tickets__title:nth-of-type(3) {
  background-color: #752f97; }

footer {
  background-color: #f53756; }
  footer div {
    flex-direction: column;
    align-items: center;
    text-align: center; }
    @media (min-width: 768px) {
      footer div {
        flex-direction: row;
        justify-content: space-between; } }
    footer div p {
      color: #fff;
      font-size: bodyFontS; }
